@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;500;600;700&family=Karla:wght@300;400;700&display=swap");

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
body {
	font-family: "Karla", sans-serif;
}
h1,
h2,
h3,
h4 {
	color: #293264;
}
.button {
	background: #4d5b9e;
	border-radius: 15px;
	font-family: "Inter", sans-serif;
	font-weight: 500;
	font-size: 16px;
	line-height: 19px;
	text-align: center;

	color: #f5f7fb;

	padding: 16px 29px;
	border: none;
	cursor: pointer;
}
.hor-wrapper {
	display: flex;
	column-gap: 20px;
	align-items: center;
	align-self: center;
}
.opening-page {
	width: 100vw;
	height: 100vh;
	background-color: #f5f7fb;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-image: url("./images/blob-sky.svg"), url("./images/blob-yelow.svg");
	background-position: left bottom, right top;
	background-repeat: no-repeat;
}
.opening-messages {
	display: flex;
	flex-direction: column;
	align-items: center;
	row-gap: 8px;
}
.opening-messages h1 {
	font-family: "Karla";
	font-style: normal;
	font-weight: bold;
	font-size: 31.25px;
	line-height: 37px;
	text-align: center;
}
.opening-messages p {
	font-family: "Inter";
	font-weight: 400;
	font-size: 16px;
	line-height: 19px;
	text-align: center;
}
.opening-messages button {
	margin-top: 21px;
}
.content-page {
	width: 100vw;
	height: 100vh;
	background-image: url("./images/blob-sky-small.svg"),
		url("./images/blob-yellow-small.svg");
	background-position: left bottom, right top;
	background-repeat: no-repeat;

	display: flex;
	flex-direction: column;
	row-gap: 45px;

	padding-left: 15vw;
	padding-right: 15vw;
	padding-top: 8vh;
	padding-bottom: 5vh;
}
.content-page button {
	width: 200px;
	align-self: center;
}
.question-item {
	padding-bottom: 15px;
	border-bottom: 1px solid #dbdef0;
	display: flex;
	flex-direction: column;
	row-gap: 12px;
}
.question-item h2 {
	font-family: "Karla";
	font-size: 16px;
	line-height: 19px;

	color: #293264;
}
.options-wrapper {
	display: flex;
	column-gap: 13px;
	margin-top: 10px;
	margin-bottom: 5px;
}
.option {
	text-decoration: none;
	text-align: center;
	font-family: "Inter";
	font-style: normal;
	font-weight: 500;
	font-size: 10.24px;
	line-height: 12px;
	color: #293264;
	min-width: 65px;
	min-height: 21px;
	padding: 4px 8px;

	border: 1px solid #4d5b9e;
	border-radius: 8px;
}

.option.option--choosen {
	border: none;
	background: #d6dbf5;
}

.content-page.result .option,
.content-page.result .option.option--choosen {
	cursor: not-allowed;
	opacity: 0.5;
	border: 1px solid #4d5b9e;
	color: #293264;
}
.content-page.result .option.option--correct {
	color: #293264;
	border: none;
	background-color: #94d7a2;
	opacity: 1;
}
.content-page.result .option.option--incorrect {
	border: none;
	color: #293264;
	background: #f8bcbc;
}

.footer {
	background-color: #f5f7fb;
	display: flex;
	flex-direction: column;
	z-index: 1000;
	width: 30vw;
	justify-self: flex-end;
	align-self: flex-end;
	position: absolute;
	margin-top: 95vh;
	padding-right: 25px;
}
.footer div.hor-wrapper {
	display: flex;
	flex-direction: row;
	column-gap: 10px;
	align-content: flex-start;
	align-self: flex-end;
}
.App {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: #f5f7fb;
	position: relative;
}
